* {
  padding: 0;
  margin: 0; }

body {
  font-family: "Cabin", sans-serif;
  letter-spacing: 0.02em;
  font-weight: 400; }

.footer {
  padding-top: 30px;
  padding-bottom: 15px;
  background-color: #383737;
  text-align: center; }
  .footer__nav-link {
    font-size: 16px;
    line-height: 36px;
    color: #fff;
    text-decoration: none; }
    .footer__nav-link:hover {
      text-decoration: underline; }
    .footer__nav-link:not(:first-child) {
      margin-left: 30px; }
      @media (max-width: 650px) {
        .footer__nav-link:not(:first-child) {
          margin-left: 0; } }
    @media (max-width: 650px) {
      .footer__nav-link {
        display: block; } }
  .footer__copyright {
    font-size: 14px;
    line-height: 36px;
    color: rgba(255, 255, 255, 0.5); }

.social-nav {
  margin: 25px 0; }

.icon {
  display: inline-block;
  vertical-align: middle;
  width: 36px;
  height: 36px;
  margin-right: 16px;
  border-radius: 50%;
  text-align: center;
  font-size: 14px;
  line-height: 36px;
  color: #FFF;
  text-decoration: none;
  transition: 0.3s; }
  .icon:last-child {
    margin-right: 0; }
  .icon--facebook {
    background: #3162a2; }
    .icon--facebook:hover {
      background-color: #295185; }
  .icon--twitter {
    background: #2eb1eb; }
    .icon--twitter:hover {
      background-color: #1D86B4; }
  .icon--dribbble {
    background: #f552b6; }
    .icon--dribbble:hover {
      background-color: #BE428E; }
  .icon--vimeo {
    background: #3498b9; }
    .icon--vimeo:hover {
      background-color: #2C7A93; }
  .icon--google-plus {
    background: #f25c58; }
    .icon--google-plus:hover {
      background-color: #C3504D; }
